import axios from 'axios';
import merge from 'deepmerge';
import { getIndexedDB } from '../utils/indexeddb';
import { datasync } from '../utils/datasync.yandex';
import { getItem, directItem } from '@/utils/item.js';
import { sortBy } from '@/utils/common.js';

const defaultState = {
  items: [],
  itemsLength: 0,
  langs: [],
  natives: [],
  dictionaryItem: null,
  indexedDB: null,
  datasync: null,
  dictionaryID: null,
  readyState: 0,
};

export default {
  namespaced: false,
  state: Object.assign({}, defaultState),
  getters: {
    items: (state) => state.items,
    itemsLength: (state) => state.itemsLength,
    langs: (state) => state.langs,
    natives: (state) => state.natives,
    dictionaryItem: (state) => state.dictionaryItem,
  },
  mutations: {
    setItems: (state, { items, langs, natives, dictionaryID }) => {
      state.items = items;
      if (langs) state.langs = langs;
      if (natives) state.natives = natives;
      if (dictionaryID) state.dictionaryID = dictionaryID;
      state.itemsLength = items.length;
    },
    dictionaryItem(state, dictionaryItem) {
      state.dictionaryItem = dictionaryItem;
    },
  },
  actions: {
    async fetchItems(store) {
      const { commit, state, dispatch, rootGetters } = store;
      const collection = rootGetters.collection;
      const { id: collectionID, path2sync } = collection || {};
      if (!collectionID) return state.items;
      if (state.dictionaryID === collectionID && state.readyState !== 0)
        return state.items;

      // init state
      for (const prop in defaultState) {
        state[prop] = defaultState[prop];
      }
      state.dictionaryID = collectionID;

      state.readyState = 1;

      const native = rootGetters.settings.training.native;
      let dbItems = [];         // words from local db
      const sourceItems = [];   // words from server
      let datasyncItemsMap = new Map(); // words from datasync.yandex
      return dispatch('fetchDictionaryDB')
        .then(function (e) {
          dbItems = e && e.target.result;
          if (!dbItems || !dbItems.length) return Promise.reject(e);
        })
        .catch(function (error) {
          dbItems = [];
          console.info('Fetch dictionary from the remote source only.\n', error);
        })
        .finally(function () {
          // fetch from the remote source
          const remotes = [dispatch('fetchDataSync')];
          if (Date.now() - collection.updated > rootGetters.settings.updateTimeout) {
            path2sync && remotes.push(axios({
                  method: 'get',
                  url: `${rootGetters.env.VUE_APP_PATH_TO_DICTIONARIES}${path2sync}`,
                  baseURL: rootGetters.env.BASE_URL,
                })
              );
          }

          return Promise.allSettled(remotes).then((results) => {
            results.forEach(({ status, value }, indx) => {
              if (status === 'rejected') return;
              // datasync: fetched
              if (indx === 0) {
                if (!value?.size) return;
                datasyncItemsMap = value;
              }
              // server: parse the Yandex.Translate saved collection
              else if (indx === 1) {
                value.data.collectionAction.recordsActions.forEach((source) => {
                  const item = getItem(source, { collectionID, native });
                  item && sourceItems.push(item);
                });
              }
            });
          });
        })
        .catch(function (e) {
          console.error(e);
        })
        .finally(() => {
          // sync local items with remote source items
          const items4Learning = [];  // words for learning
          const items4Sync = [];      // items for sync with local db
          const natives = {};         // native langs
          const langs = {};           // langs for learning
          const addItem = (items, needSync) => {
            items = [items].flat().filter((i) => i);

            const first = items.length && items[0];
            const item = merge.all(sortBy(items, 'updated'));

            if (!needSync && first)
              needSync =
                // dbItem.lang === lang && // can be changed?
                item.text === first.text &&
                item.translation === first.translation &&
                item.transcription === first.transcription &&
                item.score.value === first.score.value &&
                item.collectionID !== first.collectionID;

            rootGetters.unfoldScore(store, item);

            if (needSync) items4Sync.push(item);
            natives[item.native] = 1;
            langs[item.lang] = 1;
            items4Learning.push(item);
          };

          // Sync indexedDB and server:
          const [noDB, noSource, noDatasync] = [
            !dbItems.length,
            !sourceItems.length,
            !datasyncItemsMap.size,
          ];
          if (noDB && noSource && noDatasync) {
            commit('alert', {
              emptyCollection: { name: rootGetters.collection.name },
            });
            state.readyState = 4;
            return Promise.reject(state.items);
          } else commit('alert', { emptyCollection: undefined });

          // copy
          if (noDB) {
            if (!noSource)
              sourceItems.forEach((sourceItem) => {
                const datasyncItem = datasyncItemsMap.get(sourceItem.id)?.item;
                addItem([sourceItem, datasyncItem], true);
              });
            else datasyncItemsMap.forEach(({ item }) => addItem(item, true));
          }
          // sync
          else {
            const dbMap = new Map(dbItems.map((item) => [item.id, item]));
            const sourceMap = new Map(sourceItems.map((item) => [item.id, item]));

            for (const [id, dbItem] of dbMap) {
              const sourceItem = sourceMap.get(id);
              const datasyncItem = datasyncItemsMap.get(id)?.item;
              addItem([dbItem, sourceItem, datasyncItem]);

              if (sourceMap.size)
                sourceItem
                  ? sourceMap.delete(id)
                  : items4Sync.push([id, 'delete']);
              else if (datasyncItem)
                datasyncItem.text
                  ? datasyncItemsMap.delete(id)
                  : items4Sync.push([id, 'delete']); // check: I'm not sure if it works
            }

            // add extra item
            if (sourceMap.size)
              sourceMap.forEach((item) => addItem(item, true));
            else if (datasyncItemsMap.size)
              datasyncItemsMap.forEach(({ item }) => addItem(item, true));
          }

          commit('setItems', {
            natives: Object.keys(natives).sort(),
            langs: Object.keys(langs).sort(),
            items: items4Learning,
          });

          items4Sync.length && dispatch('syncDictionary', items4Sync);

          state.readyState = 4;
          return Promise.reject(state.items);
        })
        .catch((e) => e);
    },

    async fetchDictionaryDB({ state }) {
      state.indexedDB = getIndexedDB();
      return state.indexedDB.then(function (db) {
        return db.store('records').get({ collectionID: state.dictionaryID });
      });
    },
    async fetchDataSync({ state }) {
      state.datasync = datasync('dictionary');
      return state.datasync.get(state.dictionaryID).then((items) => items);
    },

    async syncDictionary({ state, rootGetters }, item) {
      if (!state.indexedDB || !state.items) return;
      const items = item ? [item].flat() : state.items;
      const updated = Date.now();
      items.forEach((item) => {
        directItem(item);
        item.updated = updated;
        delete item.score.info;
        delete item.score.formated;
      });

      Promise.allSettled([
        state.datasync.set(state.dictionaryID, { items, updated }),
        state.indexedDB.then((db) => {
          if (rootGetters.collection) rootGetters.collection.updated = updated;
          this.dispatch('syncCollections', rootGetters.collection);
          return db.store('records').set(items);
        }),
      ]).catch((e) => console.error(e));
    },

    async dictionaryItem({ commit, state, rootGetters }, params) {
      const { text, lang, id } = params;
      const native = rootGetters.settings.training.native;

      let dictionaryItem = id && state.items.find((item) => item.id == id);

      dictionaryItem ||= state.items.find(
        (item) => item.text === text && item.native === native && item.lang === lang
      );
      commit('dictionaryItem', dictionaryItem);
    },
  },
};
