<template>
  <div
    :class="`crossword complexity${
      easyComplexity ? '-min' : hardComplexity ? '-max' : ''
    }`"
    v-if="!$store.getters.alert && item"
  >
    <div v-if="$store.getters.crossword.isCreation" class="crossword-loader">
      <div v-for="x in [1, 2, 3]" :key="x" :class="`loader-layout-${x}`">
        <template v-for="y in [1, 2, 3]" :key="y">
          <div v-for="z in [1, 2, 3]" :key="z" :class="`cube h${x} w${y} l${z}`">
            <div class="face top"></div>
            <div class="face left"></div>
            <div class="face right"></div>
          </div>
        </template>
      </div>
    </div>
    <template v-else>
      <div class="crossword-grid">
        <b-row v-for="(row, y) in $store.getters.crossword.matrix || []" :key="y">
          <b-col v-for="(cell, x) in row" :key="x">
            <input
              v-if="cell.isUsed"
              :id="`letter-${cell.position.x}-${cell.position.y}`"
              :class="`letter letter-${cell.items.length === 1 ? 'single' : 'multi'} ${cell.items.map((item) => 'letter-id-' + item.id).join(' ')}`"
              type="text"
              autocomplete="off"
              maxlength="1"
              value=""
              :pattern="cell.value.toLowerCase()" required
              :placeholder="cell.value.toLowerCase()"
              @click="onClick(cell.items, $event)"
              @keyup="onKeyUp(cell, $event)"
              @dblclick="onDoubleClick(cell.items, $event)"
              @change="setSuccess(cell.items)"
            />
          </b-col>
        </b-row>
      </div>
      <div class="crossword-legend">
        <ul class="crossword-legend-list">
          <li
            v-for="{ item } in usedItemsMap?.values()"
            :key="item.id"
            :class="`legend legend-${item.id}`"
          >
            <a class="nav-link" @click="onClick(item, $event)">{{ item.translation }}</a>
          </li>
        </ul>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: 'Training By Crossword',
  inject: { buttons: { type: Object }, bulk: { type: Object, default: {} } },
  props: { item: { type: Object } },
  data() {
    return {
      isScored: {},

      size: { width: 15, height: 15 },
      minSize: { width: 7, height: 7 },
      defaultLength: 12,

      selectedItem: null,
      unSuccessIdSet: null,
    };
  },
  computed: {
    complexity() {
      return (
        this.$store.getters.settings.training.complexity.crossword ||
        this.$store.getters.settings.complexity.crossword.default
      );
    },
    easyComplexity() {
      return this.complexity === this.$store.getters.settings.complexity.crossword.min;
    },
    hardComplexity() {
      return this.complexity === this.$store.getters.settings.complexity.crossword.max;
    },
    usedItemsMap() {
      return this.$store.getters.crossword.usedItemsMap;
    },
    length() {
      return this.easyComplexity
        ? 10
        : this.hardComplexity
          ? 15
          : this.defaultLength;
    },
  },
  watch: {
    item() {
      this.update(true);
    },
    hardComplexity() {
      this.setSuccess();
    },
    '$store.getters.alert'(alert) {
      if (alert?.name !== 'noneCrossword') return;
      this.setButtons(true);
    },
    '$store.getters.crossword.isCreation'(state) {
      this.setButtons(!state);

      // note: check after creation check,
      // warn: can be undefined several times in a row
      if (!state) {
        if (!this.$store.getters.crossword.matrix || this.usedItemsMap?.size < 3) {
          this.$store.commit('alert', { noneCrossword: this.item });
          return;
        }
      }
    },
    selectedItem(item, lastItem) {
      if (item?.id === lastItem?.id) return;

      this.toogleClass(lastItem, 'active', false);
      this.toogleClass(item, 'active', !!item);
    },
    '$store.getters.trainingItems'() {
      this.createWordsMap(true);
    },
    '$store.getters.trainingType'() {
      this.$store.commit('alert', { noneCrossword: undefined });
    },
    'unSuccessIdSet.size'(a, b) {
      this.$store.commit('success', b && !a);
      if (b && !a) this.exam();
    },
    'buttons.right.pressed'() {
      if (!this.$store.getters.alert) this.exam();
    },
    usedItemsMap(usedItemsMap) {
      if (!usedItemsMap) return;
      this.unSuccessIdSet = new Set(usedItemsMap.keys());
    },
  },
  created() {
    window.addEventListener('click', this.onBodyClick);
  },
  mounted() {
    this.$store.commit('success', false);
    this.createWordsMap();
  },
  unmounted() {
    this.$store.commit('alert', { noneCrossword: undefined });
    window.removeEventListener('click', this.onBodyClick);
  },
  methods: {
    calcSize() {
      // note: it's approximately
      this.size.width = Math.max(
        this.minSize.width,
        Math.floor(
          (3 * document.querySelector('div:has(> .training-by-crossword)').clientWidth)
          /
          (4 * (document.querySelector('.crossword-grid .col')?.clientWidth || 48))
        )
      );
    },

    createWordsMap(force) {
      if (force || !this.$store.getters.crossword.wordsMap)
        this.$store.commit('crosswordWordsMap');
      this.update();
    },
    update() {
      this.$store.commit('alert', { noneCrossword: undefined });
      this.$store.commit('loaded', { currentTraining: true });

      this.selectedItem = null;
      this.isScored = {};

      this.toogleClass(null, 'active', false);
      this.toogleClass(null, 'success', false);

      this.calcSize();

      this.$nextTick(() =>
        this.$store.commit('buildCrossword', {
          item: this.item,
          length: this.length,
          size: this.size,
        })
      );
    },

    onKeyUp(cell, e) {
      if (e.altKey || e.ctrlKey || e.metaKey) return;

      let key = e.key.toLowerCase();
      if (key.length === 1 || key === 'unidentified') return this.goToNextLetter(cell, e);

      if (key === 'backspace')
        key =
          this.usedItemsMap.get(this.selectedItem.id).direction === 'col'
            ? 'arrowleft'
            : 'arrowup';

      let letter = null;
      const position = { x: cell.position.x, y: cell.position.y };
      switch (key) {
        case 'arrowup':
          letter = this.getLetterUp(position) || this.getLetterLeft(position);
          break;
        case 'arrowleft':
          letter = this.getLetterLeft(position) || this.getLetterUp(position);
          break;
        case 'arrowright':
          letter = this.getLetterRight(position) || this.getLetterDown(position);
          break;
        case 'arrowdown':
          letter = this.getLetterDown(position) || this.getLetterRight(position);
          break;
        default:
          return;
      }
      if (letter) return letter.click();

      this.goToNextLetter(cell, e);
    },
    getLetterLeft({ x, y }) {
      let letter = null;
      for (; --x >= 0; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    getLetterUp({ x, y }) {
      let letter = null;
      for (; --y >= 0; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    getLetterRight({ x, y }) {
      let letter = null;
      for (; ++x < this.size.width; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    getLetterDown({ x, y }) {
      let letter = null;
      for (; ++y < this.size.height; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    goToNextLetter({ items }, e) {
      this.setSuccess(items);

      const currentLetter = e.currentTarget;
      const currentId = this.selectedItem?.id;

      // info: too easy to check through
      // if (!currentLetter.classList.contains('crossword-success-item')) return currentLetter.select();

      // find the next letter after the current for the selected if unSuccess
      if (currentId && !this.checkSuccess(this.selectedItem)) {
        let isCurrentPassed = false;
        const nextLetter = [...document.querySelectorAll(`.letter-id-${currentId}`)].find((letter) => {
          if (isCurrentPassed) return letter;
          isCurrentPassed = letter === currentLetter;
        });
        if (nextLetter) return nextLetter.click();
      };

      const findLetterExcludeCurrent = (items, noId) => {
        return items?.find((item) => {
          if (!item || item.id === noId) return;
          if (!this.checkSuccess(item)) {
            const nextLetter = [
              ...document.querySelectorAll(`.letter-id-${item.id}:not(.crossword-success-item)`)
            ][0];
            if (!nextLetter) return;

            this.selectedItem = item;
            nextLetter.click();
            return nextLetter;
          }
        });
      };

      // find the letter for unSuccess item among current cellmate
      if (findLetterExcludeCurrent(items, currentId)) return;

      // find the letter for unSuccess item among all cellmates
      if (
        [...document.querySelectorAll(`.letter-id-${currentId}.letter-multi`)].find((letter) => {
          return findLetterExcludeCurrent(
            letter.className
              .replace(/.*?letter-id-([^\s]+).*?/g, '$1,')
              .split(',')
              .map((id) => this.usedItemsMap.get(id)?.item)
          , currentId);
        })
      ) return;

      // find the letter among all unSuccess items
      if (findLetterExcludeCurrent( [...this.unSuccessIdSet].map((id) => this.usedItemsMap.get(id)?.item) ))
        return;

      // all Success
      currentLetter.blur();
      document.body.click();
    },

    scrollIntoViewIfNeeded(el) {
      if (!el) return;
      el[el['scrollIntoViewIfNeeded'] ? 'scrollIntoViewIfNeeded' : 'scrollIntoView']();
    },

    onBodyClick() {
      this.selectedItem = null;
    },
    onClick(items, e) {
      e?.stopImmediatePropagation();

      items = [items].flat();

      // choose new selectedItem
      let hasCurrent = false;
      let isSuccessCurrent = false;
      const currentId = this.selectedItem?.id;
      const unSuccessItems = items.filter((item) => {
        const isCurrent = currentId === item.id;
        if (isCurrent) hasCurrent = true;
        if (this.checkSuccess(item)) {
          if (isCurrent) isSuccessCurrent = true;
          return;
        }
        return !isCurrent;
      });
      const item = hasCurrent && !isSuccessCurrent
        ? this.selectedItem                 // selected has && unsuccess  don't change it
        : (unSuccessItems[0] || items[0]);  // new unselected item  unsucces || success

      let currentTarget = e?.currentTarget?.nodeName === 'INPUT'
          ? e.currentTarget
          : document.querySelector(`.letter-id-${item.id}`);
      currentTarget.select();

      this.selectedItem = item;
      this.scrollIntoViewIfNeeded(currentTarget);
    },
    onDoubleClick(items, e) {
      if (items.length < 2) return;
      this.onClick(items.find((item) => item.id !== this.selectedItem.id), e);
    },

    checkSuccess(item) {
      if (!item) return false;
      const isSuccess = [...document.querySelectorAll(`.letter-id-${item.id}`)]
          .reduce((text, input) => text + (input.value || ' '), '')
          .toLowerCase() === item.text.toLowerCase();
      return isSuccess;
    },
    setSuccess(item) {
      (item ? [item].flat() : this.usedItemsMap)?.forEach((value) => {
        const item = value?.item || value;
        const isSuccess = this.checkSuccess(item);
        this.unSuccessIdSet[isSuccess ? 'delete' : 'add'](item.id);
        this.toogleClass(item, 'success', isSuccess);
        if (!isSuccess) return;

        this.exam(item);
        this.$speak(item);
      });
    },

    toogleClass(item, className, force) {
      const addIdClass = item ? `-${item.id}` : '';

      document
        .querySelectorAll(`.legend${addIdClass}, .letter-id${addIdClass}`)
        .forEach((el) => {
          // no ruin cellmate
          const specialForce =
            className === 'success' &&
            el.nodeName === 'INPUT' &&
            el.classList.contains('letter-multi') &&
            this.checkSuccess(
              this.usedItemsMap.get(
                el.className
                  .replace(addIdClass, '')
                  .replace(/.*letter-id-([^\s]+).*/g, '$1')
              )?.item
            );
          el.classList.toggle(`crossword-${className}-item`, force || specialForce);
        });
    },
    setButtons(isVisible) {
      this.buttons?.set({
        left: {
          visible: isVisible,
        },
        right: {
          visible: isVisible,
        },
      });
    },
    exam(item) {
      (item ? [item].flat() : this.usedItemsMap)?.forEach((value) => {
        const { id } = value?.item || value;
        if (this.isScored[id]) return;
        this.usedItemsMap.get(id).item.score.exam({
          name: 'crossword',
          result: this.unSuccessIdSet.has(id)
            ? 'error'
            : (this.bulk.isSpoiled ? 'spoiled' :'success'),
        });
        this.isScored[id] = true;
      });
    },
  },
};
</script>
