import { reverseItem, directItem } from '@/utils/item.js';
import { buildCrossword, generateLettersMap } from '@/utils/crosswords-generator.js';

export default {
  namespaced: false,
  state: {
    trainingItems: [],

    bulkItems: [],
    bulkLength: 0,
    indexTrainingItem: 0,

    pickingItems: [],
    crossword: {
      isCreation: false,
      wordField: 'text',
      minCount: 15,

      itemId: null,
      wordsMap: null,
      matrix: null,
      usedItemsMap: null,
    },

    trainingType: null,
    isSuccess: false,
    loaded: {},
  },
  getters: {
    trainingItems: (state) => state.trainingItems,

    bulkItems: (state) => state.bulkItems,
    bulkLength: (state) => state.bulkLength,
    trainingItem: (state) => state.bulkItems[state.indexTrainingItem],
    indexTrainingItem: (state) => state.indexTrainingItem,

    pickingItems: (state) => state.pickingItems,
    crossword: (state) => state.crossword,

    trainingType: (state) => state.trainingType,
    loaded: (state) => state.loaded, // fix: remove!
    isSuccess: (state) => state.isSuccess,
    isLoaded: (state) =>
      Object.values(state.loaded).every((val) => val || val === undefined),
    results: (state) => {
      const results = [state.bulkItems];
      switch (state.trainingType) {
        case 'picking':
          state.pickingItems.length && results.push(state.pickingItems);
          break;
        case 'crossword':
          state.crossword.usedItemsMap?.size &&
            results.push([...state.crossword.usedItemsMap.values()]);
          break;
      }
      return results;
    },
  },
  mutations: {
    trainingItems(state) {
      if (!this.getters.settings || !this.getters.itemsLength) return;
      const { score, training } = this.getters.settings;
      const { native, lang, type, textType, useReverseTranslation } = training;

      const items = this.getters.items.filter((item) => {
        if (item.score.data[type] >= (score.limit[type] || score.limit.default)) return;
        if (textType && textType !== item.textType) return;

        if (useReverseTranslation) {
          if (native === item.lang && (!lang || lang === item.native)) {
            reverseItem(item);
          }
        } else directItem(item);

        if (native !== item.native) return;
        if (lang && lang !== item.lang) return;

        return true;
      });

      state.trainingItems = items;
      this.commit('setBulkOptions');
    },
    setBulkOptions(state) {
      if (!this.getters.settings) return;
      const { training } = this.getters.settings;

      // select N random items for training
      const bulkItems = [...this.getters.trainingItems].sort(() => 0.5 - Math.random());
      state.bulkItems = training.count ? bulkItems.slice(0, training.count) : bulkItems;
      state.bulkLength = state.bulkItems.length;
      state.indexTrainingItem = 0;

      this.commit('alert', { noneItems: !state.bulkLength || undefined });
    },
    pickingItems(state, params) {
      const count = params.count;
      if (count && count < 1) return;

      const pickingItems = [];
      const { id, translation } = params.item;
      const items = [...this.getters.trainingItems].sort(() => 0.5 - Math.random()); // check: too much?
      for (
        let i = 0, counter = 1, n = items.length;
        i < n && (count ? counter < count : true);
        i++
      ) {
        const item = items[i];
        if (item.id === id) continue;                         // add self after
        if (item.translation === translation) continue;       // synonyms

        pickingItems.push(item) && counter++;
      }

      pickingItems.push(params.item);
      pickingItems.sort(() => 0.5 - Math.random());

      state.pickingItems = pickingItems;
    },
    crosswordWordsMap(state) {
      state.itemId = null;
      state.matrix = null;
      state.usedItemsMap = null;
      state.crossword.wordsMap = generateLettersMap({
        items: state.trainingItems,
        wordField: state.crossword.wordField,
      });
    },
    buildCrossword(state, options) {
      if (state.crossword.id === options.item.id) return;

      state.crossword.isCreation = true;
      setTimeout(() => {
        try {
          var { matrix, usedItemsMap } =
            buildCrossword({
              wordsMap: state.crossword.wordsMap,
              items: [options.item],
              wordField: state.crossword.wordField,
              size: options.size,
              length: options.length,
            }) || {};
        }  catch(e) { console.error(e); }

        // cut empty
        const starts = [];
        if (matrix) {
          matrix = matrix
              .map((row) => {
              starts.push(row.findIndex(({ value }) => !!value));
              return row.splice(0, row.findLastIndex(({ value }) => !!value) + 1);
            })
            .filter((row) => row.length);
          const startIndexCell = Math.min(...starts.filter((i) => i !== -1));
          matrix.map((row) => row.splice(0, startIndexCell));
        }

        state.crossword.id = options.item.id;
        state.crossword.matrix = matrix; // has cutted items
        state.crossword.usedItemsMap = usedItemsMap; // { id: {item }, { direction } }

        state.crossword.isCreation = false;
      }, 0);
    },
    nextItem: function (state) {
      if (this.getters.trainingItem) {
        if (state.indexTrainingItem < state.bulkLength) state.indexTrainingItem++;
      }
    },
    trainingType: function (state, trainingType) {
      this.commit('alert', { noneNetwork: undefined }); // don't need for offline trainings
      state.trainingType = trainingType;
    },
    success: (state, isSuccess) => {
      state.isSuccess = isSuccess;
    },
    loaded: (state, item) => {
      Object.keys(item).forEach((key) => {
        if (item[key] === undefined) {
          delete state.loaded[key];
        } else {
          state.loaded[key] = item[key];
        }
      });
    },
  },
};
