<template>
  <!-- results -->
  <b-tabs
    v-if="shownResult"
    :class="`training-results tabs-count-${results.length}`"
    :modelValue="results.length - 1"
  >
    <b-tab
      v-for="(items, i) in $store.getters.results"
      :key="i"
      tag="table"
      class="table"
      justified
      :title="$t(`training.results.tab-${i}`)"
    >
      <tbody>
        <tr v-for="item in items" :key="(item = item.item || item, item.id)">
          <td>
            <div class="training-results-text">
              <lang-icon :lang="item.lang" />
              {{ item.text }}
            </div>
          </td>
          <td>
            {{ item.translation }}
          </td>
          <td>
            <training-actions
              :item="item"
              :actions="['article']"
              :isShortLink="true"
            ></training-actions>
          </td>
          <td>
            <training-actions
              :item="item"
              :actions="['score', 'clear']"
            ></training-actions>
          </td>
        </tr>
      </tbody>
    </b-tab>
  </b-tabs>

  <!-- training -->
  <div class="training-desk">
    <keep-alive>
      <training v-if="!shownResult"></training>
    </keep-alive>
  </div>
</template>

<script>
import merge from 'deepmerge';
import TrainingActions from '@/components/Training/Actions';
import LangIcon from '@/components/LangIcon/LangIcon';
import Training from '@/views/Training/Training';

export default {
  name: 'Bulk Training',
  inject: ['buttons'],
  components: {
    TrainingActions,
    LangIcon,
    Training,
  },
  provide() {
    return {
      bulk: this.$data, // shownResult by link
    };
  },
  data() {
    return {
      shownResult: false,
      isSpoiled: false,
    };
  },
  computed: {
    isSuccess() {
      return this.$store.getters.isSuccess;
    },
    item() {
      return this.$store.getters.trainingItem;
    },
    results() {
      return this.$store.getters.results;
    },
  },
  watch: {
    '$store.getters.settings.training.count'() {
      this.update();
    },
    '$store.getters.listening.enabled'() {
      this.update();
    },
    'buttons.left.pressed'() {
      this.onPressButton('left');
    },
    'buttons.right.pressed'() {
      this.onPressButton('right');
    },
    '$store.getters.trainingType'() {
      this.clear(true);
    },
    '$store.getters.dictionaryItem'() {
      this.clear();
    },
    item(item) {
      if (!item) return;
      this.isSpoiled = 0;
      this.clear();
    },
    isSuccess() {
      this.setButtons();
    },
    shownResult(isShow) {
      if (isShow && !this.isSuccess) ++this.isSpoiled;
      this.setButtons();
    },
  },
  unmounted() {
    this.$store.commit('alert', { noneItems: undefined });
  },
  methods: {
    clear(withButton) {
      this.shownResult = false;
      this.setButtons(withButton);
    },
    next() {
      if (this.item) {
        if (this.shownResult) {
          this.shownResult = false;
          return;
        }

        const successType = this.buttons.right.type;
        if (
          this.isSuccess ||
          successType === 'skip' ||
          successType === 'correct' ||
          successType === 'incorrect' ||
          successType === 'continue'
        ) {
          this.$store.commit('nextItem');
          if (!this.item) this.shownResult = true;
        }
      } else this.update();
    },
    update() {
      if (!this.$store.getters.itemsLength) return; // before dictionary loaded

      this.$store.commit('setBulkOptions');
      this.clear(true);
    },
    setButtons(withClear) {
      let buttons = this.shownResult
        ? {
            // note: it's a hack for score.exam
            left: {
              type: this.item ? 'continue' : 'restart',
            },
            right: {
              visible: false,
            },
          }
        : {
            left: {
              type: 'results',
            },
            right: {
              type: (!withClear && this.buttons.right.specialType) || (this.isSuccess ? 'continue' : 'skip'),
              visible: true,
            },
          };
      if (withClear) buttons = merge.all([this.buttons._default, buttons]);
      this.buttons.set(buttons);
    },
    onPressButton(name) {
      switch (this.buttons[name].type) {
        case 'check':
          return;
        case 'results':
          this.shownResult = true;
          return;
        case 'continue':
          if (this.shownResult) {
            this.shownResult = false;
            return;
          }
        // case 'skip':
        // case 'restart':
        // case 'correct':
        // case 'incorrect':
      }
      return this.next();
    },
  },
};
</script>
