import axios from 'axios';
import merge from 'deepmerge';
import { sortBy } from '@/utils/common.js';

const {
  token = window.localStorage.getItem('auth-yandex-token'),
  context = 'app',
  baseURL = `https://cloud-api.yandex.net/v1/data/${context}/databases/`,
  headers = {
    // Header:
    // Authorization: OAuth e78cf1a0ebbb19...
    Authorization: `OAuth ${token}`,
    // 'Access-Control-Allow-Origin': '*',
    // 'Access-Control-Allow-Methods': 'HEAD, GET, POST, PUT, PATCH, DELETE, OPTIONS',
    // 'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method,Access-Control-Request-Headers, Authorization',
  },
} = {};

const parseName = (id) => {
  const matches = (/^((settings)|(collections)|((dictionary)-(.+)))$/.exec(id))||[];
  const collectionID = matches[matches.length - 1];
  const name = matches[collectionID ? matches.length - 2 : 0];
  return { name, collectionID };
};

const inited = {};
const database = (name) => {
  const base_revision = (name) => {
    const id = `datasync-yandex-${name}-revision`;
    let _value = window.localStorage.getItem(id) || 0;
    return {
      toString() {
        return _value;
      },
      valueOf() {
        return _value;
      },
      get() {
        return _value;
      },
      set(revision) {
        window.localStorage.setItem(id, revision);
        _value = revision;
      },
    };
  };

  const data = {
    settings: () => {
      const id = 'settings';
      inited[id] ||= {
        id,
        revision: base_revision(id),
        collection: {
          id: 'records',
          fields: {
            id: { field_id: 'id', value: { type: 'string' } },
            data: { field_id: 'data', value: { type: 'string' } },
          },
        },
        info: null,
      };
      return inited[id];
    },
    storage: () => {
      const id = 'storage';
      inited[id] ||= {
        id,
        revision: base_revision(id),
        collection: {
          id: 'daySummary',
          fields: {
            day: { field_id: 'day', value: { type: 'integer' } },
            summary: { field_id: 'summary', value: { type: 'double' } },
          },
        },
        info: null,
      };
      return inited[id];
    },
    dictionary: (collectionID) => {
      const id = `dictionary-${collectionID}`;
      inited[id] ||= {
        id,
        revision: base_revision(id),
        collection: {
          id: collectionID,
          fields: {
            id: { field_id: 'id', value: { type: 'string' } },
            score: { field_id: 'score', value: { type: 'string' } }, // score.data
            data: { field_id: 'data', value: { type: 'string' } }, // text, translation, transcription, lang, native
          },
        },
        info: null,
      };
      return inited[id];
    },
  };

  return data[name];
};
// create:
// PUT : https://cloud-api.yandex.net/v1/data/<context>/databases/<database_id>/?[fields=<>]

// list:
// GET : https://cloud-api.yandex.net/v1/data/<context>/databases/?[fields=<>]&[limit=<>]&[offset=<>]

// save:
