<template>
  <header>
    <lang-icon :lang="item.lang"></lang-icon>
    <div class="title" v-html="wbrTitle"></div>
    <wbr/>
    <speech
      :text="item.text"
      :lang="item.lang"
      :autospeak="autospeak"
      :size="$store.getters.settings.training.mode.listening && !$store.getters.isSuccess ? 'md' : 'sm'"
      hotkey="Alt+S"
    ></speech>
    <em v-if="item.transcription" class="transcription">{{ item.transcription }}</em>
  </header>
</template>

<script>
import Speech from '@/components/Speech/Speech';
import LangIcon from '@/components/LangIcon/LangIcon';

export default {
  name: 'Article Title',
  props: {
    item: { type: Object },
    title: { type: String },
    autospeak: { type: [String, Boolean] },
  },
  components: {
    LangIcon,
    Speech,
  },
  computed: {
    wbrTitle() {
      return (this.title || this.item.text).trim().split(/\s+/).join(' <wbr>');
    },
  },
};
</script>
