<template>
  <!-- Alert -->
  <alert :params="currentItem" />

  <!-- Spinner -->
  <div v-if="!alert && !isLoaded" class="spinner-container">
    <b-spinner class="loader" variant="warning"></b-spinner>
  </div>

  <!-- Training -->
  <section
    v-show="!alert && isLoaded"
    v-if="currentItem"
    :class="`training training-by-${type} ${isSuccess ? '' : 'un'}success-training`">
    <b-row class="training-title">
      <b-col>
        <training-header
          :item="currentItem"
          :title="title || (type === 'article' ? currentItem.text : currentItem.translation)"
        />
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <component
          @fixed:title="setTitle"
          :is="`training-by-${type}`"
          :item="currentItem"
        ></component>
      </b-col>
    </b-row>
  </section>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';
import Article from '@/views/Article/Article';
import Alert from '@/components/Alert/Alert';
import TrainingHeader from '@/components/Training/Header';
import TrainingByCard from '@/components/Training/Card';
import TrainingByCrossword from '@/components/Training/Crossword';
import TrainingByDocking from '@/components/Training/Docking';
import TrainingByPicking from '@/components/Training/Picking';
import TrainingByWriting from '@/components/Training/Writing';

export default {
  name: 'Training',
  components: {
    TrainingHeader,
    Alert,
    IconifyIcon,
    'training-by-article': Article,
    'training-by-card': TrainingByCard,
    'training-by-crossword': TrainingByCrossword,
    'training-by-docking': TrainingByDocking,
    'training-by-picking': TrainingByPicking,
    'training-by-writing': TrainingByWriting,
  },
  data() {
    return {
      title: null,
    };
  },
  computed: {
    isSuccess() {
      return this.$store.getters.isSuccess;
    },
    isLoaded() {
      return this.$store.getters.isLoaded;
    },
    alert() {
      return this.$store.getters.alert;
    },
    type() {
      const type = this.$options.components[`training-by-${this.$route.params.type}`]
        ? this.$route.params.type
        : this.$store.getters.trainingType ||
          this.$store.getters.settings.training.type;

      if (this.$route.meta.active === 'training')
        this.$store.commit('trainingType', type);

      return type;
    },
    currentItem() {
      return this.$route.params.id || this.$route.params.text
        ? this.$store.getters.dictionaryItem
        : this.$store.getters.trainingItem;
    },
  },
  watch: {
    '$store.getters.settings.updated'() {
      switch (this.$store.getters.settings.lastUpdated) {
        case 'textType':
        case 'lang':
        case 'native':
        case 'useReverseTranslation':
          this.$store.commit('trainingItems');
      }
    },
    '$store.getters.collection.id'(collectionID) {
      // may be changed in Article
      collectionID && this.$store.commit('clearTempSettings');
      // this.update(); // dublicated items
    },
    '$store.getters.items'() {
      this.$store.commit('trainingItems', true);
      this.update();
    },
    currentItem() {
      this.setAlert();
      this.setTitle();
    },
    type() {
      this.setTitle(); // training/article
    },
  },
  beforeCreate() {
    this.$store.commit('loaded', { currentTraining: false });
  },
  async created() {
    if (!this.$store.getters.trainingItems.length) this.$store.commit('trainingItems');

    await this.update();
    this.setTitle();

    this.$watch(
      () => this.$route.params,
      async (newParams, oldParams) => {
        if (!newParams.type) return;
        if (
          newParams.id === oldParams.id &&
          newParams.text === oldParams.text &&
          newParams.type === oldParams.type
        )
          return;
        this.$store.commit('trainingType', this.type);
        await this.update();
      }
    );
  },
  methods: {
    async update() {
      if (!this.$store.getters.itemsLength) return; // before dictionary loaded

      await this.$store.dispatch('dictionaryItem', this.$route.params);

      this.setAlert();
    },
    setAlert() {
      this.$store.commit('alert', {
        noneDictionaryItem:
          !this.currentItem &&
          (this.$route.params.id || this.$route.params.text)
            ? this.$store.getters.dictionaryItem || this.$route.params
            : undefined,
      });
    },
    setTitle(title) {
      if (this.$store.getters.listening.on && this.currentItem)
        this.$speak(this.currentItem);

      this.title = title || null;
      this.$store.commit('title', () => {
        return this.currentItem
          ? this.$t(
              `${this.type === 'article' ? this.type : 'training'}.title`,
              title ? { title } : this.currentItem
            )
        : this.$t('training.name')
      });
    },
  },
};
</script>
